    }
    button.btn:active{
    
        -webkit-animation: wizard-button .5s ease-in-out forwards;
    
                animation: wizard-button .5s ease-in-out forwards;
    }
    
     @-webkit-keyframes wizard-button {
            0%   {
                -webkit-transform:scale(0.9);
                        transform:scale(0.9);
                color:white;
                background-color:#c05c0b;
            }
           
            50%  {
                -webkit-transform:scale(1.02);
                        transform:scale(1.02);
            color: #f2a05d;
            background-color:white !;
        }
            
        }
    
     @keyframes wizard-button {
            0%   {
                -webkit-transform:scale(0.9);
                        transform:scale(0.9);
                color:white;
                background-color:#c05c0b;
            }
           
            50%  {
                -webkit-transform:scale(1.02);
                        transform:scale(1.02);
            color: #f2a05d;
            background-color:white !;
        }
            
        }
        
